/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #eaebec;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 98%;
  margin: 0 auto;
}

.contenedor-login {
  width: 50%;
  margin: 5rem auto;
  background-color: #FFFFFF;
  align-items: center;
  padding: 2rem;
  border-radius: 1rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3,
h4 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.fondo-imagen {
  background-image: url(/build/img/app/fondo-login.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
}

.degradado-azul {
  background-color: #2b74af;
}

.seccion {
  margin-top: 2.5rem 0;
  margin-bottom: 2.5rem 0;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.gradiente {
  background: rgb(1, 59, 106);
  background: -moz-linear-gradient(180deg, rgb(1, 59, 106) 0%, rgb(0, 96, 172) 100%);
  background: -webkit-linear-gradient(180deg, rgb(1, 59, 106) 0%, rgb(0, 96, 172) 100%);
  background: linear-gradient(180deg, rgb(1, 59, 106) 0%, rgb(0, 96, 172) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#013b6a", endColorstr="#0060ac", GradientType=1);
}

.flex-end {
  display: flex;
  justify-content: end;
}

.flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex-around {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

