  text-align: center;
  margin: 1.5rem 0 0 0;
  border: none;
  border-radius: 0.5rem;
  width: 50%;
}
.boton-sesion:hover {
  background-color: #619800;
  cursor: pointer;
  color: #FFFFFF;
}

.boton-eliminar {
  background-color: #FFFFFF;
  color: #0060ac;
  font-weight: none;
  font-size: 2rem;
  text-decoration: none;
  text-align: center;
  margin: 0.2rem 0 0 0;
  padding: 0.2rem 0;
  border: none;
  display: inline-block;
  border-radius: 0.2rem;
}
.boton-eliminar:hover {
  background-color: #f2f2f2;
  cursor: pointer;
  color: red;
}

.boton-editar {
  background-color: #FFFFFF;
  color: #0060ac;
  font-weight: none;
  font-size: 2rem;
  text-decoration: none;
  text-align: center;
  margin: 0.2rem 0 0 0;
  padding: 0.2rem 0;
  border: none;
  display: inline-block;
  border-radius: 0.2rem;
}
.boton-editar:hover {
  background-color: #f2f2f2;
  cursor: pointer;
  color: #71B100;
}

.dark1-mode {
  background-color: #262626;
}
.dark1-mode p {
  color: #FFFFFF;
}
.dark1-mode a, .dark1-mode h1, .dark1-mode h2, .dark1-mode h3, .dark1-mode h4 {
  color: #eaebec;
}
.dark1-mode .anuncio,
.dark1-mode .resumen-propiedad {
  background-color: #3f3f3f;
  border: 1px solid #3f3f3f;
}
.dark1-mode .anuncio .precio,
.dark1-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark1-mode .anuncio .icono,
.dark1-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark1-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark1-mode .formulario label,
.dark1-mode .formulario legend,
.dark1-mode .formulario p {
  color: #eaebec;
}
.dark1-mode .formulario input:not([type=submit]),
.dark1-mode .formulario textarea,
.dark1-mode .formulario select {
  background-color: #3f3f3f;
  border: 1px solid #3f3f3f;
  color: #FFFFFF;
}

/** UI **/
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #0060ac;
  padding: 0.5rem 0 0.5rem 0;
  margin-top: 0rem;
}
.header .barra a {
  color: #eaebec;
}
.header .barra a img {
  height: 7rem; /*min-width: 20rem*/
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  padding: 0;
  width: 100%;
  background-color: #0060ac;
  margin: 0;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 0.5rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #eaebec;
}

/** Menu de hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

/*Menu Usuario*/
nav {
  margin: 0 auto;
  width: inherit;
  height: auto;
  display: inline-block;
  background: none;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  float: left;
  display: inline-block;
}

nav ul li {
  position: relative;
  margin: 0;
  float: left;
  display: inline-block;
}

nav ul li a {
  padding: 0.2rem 1rem;
  display: inline-block;
  color: #FFFFFF;
  text-decoration: none;
}

nav ul li a:hover {
  opacity: 0.5;
}

nav ul li ul {
  display: none;
  position: absolute;
  left: 0;
  background: #2b74af;
  float: left;
}

nav ul li ul li {
  width: 100%;
  border-bottom: 1px solid #eaebec;
}

nav ul li:hover ul {
  display: block;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
  width: 10rem;
  display: block;
  margin: 0 auto;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #f7f8f8;
  border: 1px solid #cfd2d4;
  margin-bottom: 2rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #E08709;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.formulario {
  margin-bottom: 2rem;
}
.formulario legend {
  color: #0060ac;
}
.formulario fieldset {
  border-radius: 1rem;
  border: 1px solid #0060ac;
  color: #0060ac;
}
.formulario p {
  font-size: 1.4rem;
  color: #3f3f3f;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #3f3f3f;
}
.formulario label {
  font-weight: 700;
  text-transform: none;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #eaebec;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.formulario-login {
  padding: 2rem;
}

.logo {
  width: 15rem;
  margin: 2rem auto;
}

/** Paginas Internas **/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.alerta {
  padding: 0.5rem 0 0 0;
  text-align: center;
  font-weight: bold;
  text-transform: none;
  margin: 0.5em 0;
}
.alerta.error {
  background-color: #f7cbcb;
  color: red;
}
.alerta.exito {
  background-color: #71B100;
  color: #FFFFFF;
}
.alerta.error-login {
  background-color: #f7cbcb;
  color: red;
}

.imagen-small {
  width: 20rem;
}

table.informe {
  margin-top: 1rem;
  width: 100%;
  border-spacing: 0;
}
table.informe thead {
  background-color: #0060ac;
  width: 25%;
}
table.informe thead th {
  color: #FFFFFF;
  padding: 0.2rem;
}
table.informe tbody tr td {
  border: 1px solid #eaebec;
  width: inherit;
  background-color: #FFFFFF;
  padding: 0.1rem 0.5rem;
}
table.informe tbody tr td span {
  color: #0060ac;
  padding: 0.2rem 0;
}
table.informe tbody img {
  width: 5rem;
  margin: auto;
}
table.informe .imagen-tabla {
  width: 5rem;
}

.boton-imagen {
  height: 5rem;
  width: inherit;
  display: inline;
  margin: 0 1rem 0 0;
}

