.fondo_transparente {
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.226);
  position: absolute;
  height: 100vh;
  width: 100%;
  display: none;
}

.modal {
  background: linear-gradient(0deg, #FFFFFF 80%, #0060ac 20%);
  width: 70rem;
  height: 50rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  border-radius: 0;
}

.modal_cerrar {
  background: white;
  position: absolute;
  right: -20px;
  color: #0060ac;
  top: -20px;
  width: 40px;
  height: 40px;
  border-radius: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.modal_titulo {
  color: white;
  font-size: 2rem;
}

.modal_mensaje {
  padding: 10px 30px;
}

.modal_botones {
  width: 100%;
  background: whitesmoke;
  border-top: whitesmoke 1px;
  padding-top: 20px;
  display: flex;
  justify-content: space-evenly;
}

